import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import {
    Box,
    Card,
    CardContent,
    CardHeader,
    Divider,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Grid
} from '@material-ui/core';
import TimeTrans from '../../../unit/timeTrans';
import getCookie from '../../../unit/getCookie'
import { clearAllCookie } from '../../../unit/clearAllCookie';

class BacicsChartList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            chartTitle: '',
            ChartList: [],
            selectValue: '',
            chartDbName: '',
            TimeList: [],
        };
    }

    callThis = (e) => {
        this.setState({ selectValue: e.target.value });
    }

    loadModuleDataFromUrl = (time) => {
        if (this.state.selectValue !== '') {
            time = this.state.selectValue
        } else {
            time = '5minutes'
        }

        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))

        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/chart-list/" + this.props.chartDbName + "?time=" + time + "&host=" + this.props.host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    var headres = (this.props.chartHeaders.length !== 0) ? this.props.chartHeaders : {}
                    let keyList = []
                    let DatetimeList = []
                    for (var key in result.data[1][0]) {
                        if (key !== "id" && key !== "host" && key !== "device" && key !== "total" && key !== "datetime") {
                            keyList.push(key)
                        }
                    }

                    for (let i = 0; i < result.data[1].length; ++i) {
                        DatetimeList.push(TimeTrans(result.data[1][i].datetime))
                    }


                    let allinfo = []
                    for (let i = 0; i < result.data.length / 2; ++i) {
                        let listInfo = []
                        var name = result.data[i + i]
                        var infoList = result.data[i + i + 1]
                        for (let x = 0; x < keyList.length; ++x) {
                            let test = []
                            for (let u = 0; u < infoList.length; ++u) {
                                test.push(infoList[u][keyList[x]])
                            }
                            var modelName = (headres[keyList[x]] !== undefined) ? headres[keyList[x]] : keyList[x]

                            var deviceInfo = {
                                name: modelName,
                                data: test
                            }
                            listInfo.push(deviceInfo)
                        }

                        var allDeviceInfo = {
                            name: name,
                            info: listInfo,
                            TimeList: DatetimeList
                        }
                        allinfo.push(allDeviceInfo)
                    }

                    this.setState({
                        ChartList: allinfo,
                    });
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    }

    handleChange = (e) => {
        this.setState({
            selectValue: e.target.value
        });
        setTimeout(this.loadModuleDataFromUrl, 0);
    }

    componentDidMount() {
        this.loadModuleDataFromUrl(300);
        setInterval(this.loadModuleDataFromUrl, 5000, this.state.selectValue);
    };

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        var unit = '(单位：' + this.props.chartUnit + ')';

        var title = (this.props.chartUnit !== '') ? this.props.chartTitle + '监控' + unit : this.props.chartTitle + '监控'
        const { ChartList } = this.state;

        const chart =
            ChartList.map((data, index) =>
                <Box
                    key={index}
                    sx={{
                        hight: '50%',
                        width: '48%',
                        display: 'inline-block',
                        justifyContent: 'flex-end',
                        p: 1,
                        margin: 1
                    }}>

                    <Card>
                        <CardHeader
                            title={data.name}
                        />
                        <Divider />
                        <CardContent>
                            <div>
                                <HighchartsReact
                                    highcharts={Highcharts}
                                    options={{
                                        title: {
                                            text: null
                                        },
                                        yAxis: {
                                            title: {
                                                text: null
                                            }
                                        },
                                        xAxis: {
                                            categories: data.TimeList,
                                            labels: {
                                                enabled: false

                                            }
                                        },
                                        tooltip: {
                                            pointFormat: '<span style="color:{series.color}">{series.name}</span>: <b>{point.y:.1f}' + this.props.chartUnit + '</b><br/>',
                                            shared: true
                                        },
                                        legend: {
                                            layout: 'vertical',
                                            align: 'right',
                                            verticalAlign: 'middle'
                                        },
                                        credits: {
                                            enabled: false
                                        },
                                        plotOptions: {
                                            series: {
                                                label: {
                                                    connectorAllowed: false
                                                },
                                            }
                                        },
                                        series: data.info,
                                        responsive: {
                                            rules: [{
                                                condition: {
                                                    maxWidth: 100
                                                },
                                                chartOptions: {
                                                    legend: {
                                                        layout: 'horizontal',
                                                        align: 'center',
                                                        verticalAlign: 'bottom'
                                                    }
                                                }
                                            }]
                                        }
                                    }} />
                            </div>
                        </CardContent>
                    </Card>
                </Box>
            );

        const chartList =
            <Card>
                <Box
                    sx={{
                        display: 'flex',
                        p: 1
                    }}>
                    <CardHeader
                        title={title}
                        sx={{ width: 200 }}
                    />
                    <div style={{ width: "81%" }}>
                    </div>
                    <FormControl>
                        <InputLabel>时间范围</InputLabel>
                        <Select
                            label="时间范围"
                            defaultValue='5minutes'
                            onChange={this.handleChange}
                        >
                            <MenuItem value={'5minutes'}>5分钟</MenuItem>
                            <MenuItem value={'10minutes'}>10分钟</MenuItem>
                            <MenuItem value={'30minutes'}>30分钟</MenuItem>
                        </Select>
                    </FormControl>
                </Box>
                <Divider />

                <CardContent>
                    <Grid
                        container
                        spacing={3}
                    >
                        <Grid
                            item
                            xl={12}
                            xs={12}
                        >
                            <div style={{ height: 550, overflow: 'auto' }}>
                                {chart}
                            </div>
                        </Grid>
                    </Grid>
                </CardContent>
                <Divider />
            </Card>

        return chartList
    }
}

export default BacicsChartList;