export default function TimeTrans(datetime) {
   let Time = new Date(datetime);
   let FormattedDateTime = Time.getFullYear() + '-' + (Time.getMonth() + 1) + '-' + Time.getDate() + ' ' + Time.getHours() + ':' + Time.getMinutes() + ':' + Time.getSeconds();
   
    return FormattedDateTime;
}