import React from 'react';
import {
    Card,
    CardContent,
    Grid,
    Typography
} from '@material-ui/core';
import getCookie from '../../../unit/getCookie'
import { clearAllCookie } from '../../../unit/clearAllCookie';

class BasicsTag extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Data: String,
            chartTitle: '',
            chartDbName: '',
            chartStat: false,
        };
    }

    loadModuleDataFromUrl = () => {
        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))
        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/" + this.props.chartDbName + "/new?host=" + this.props.host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    for (var i in result.data) {
                        var key = i;
                        if (key !== "id" && key !== "datetime" && key !== "host") {
                            var val = result.data[i];
                        }
                    }
                    this.setState({
                        Data: val
                    });
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    }

    componentDidMount() {
        this.loadModuleDataFromUrl();
        this.interval = setInterval(this.loadModuleDataFromUrl, 5000);
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        var Data = this.state.Data;
        var tag;

        if (this.props.chartStat === false) {
            tag =
                <Card>
                    <CardContent>
                        <Grid
                            container
                            spacing={3}
                            sx={{ justifyContent: 'space-between' }}
                        >
                            <Grid item>
                                <Typography
                                    color="textSecondary"
                                    gutterBottom
                                    variant="h3"
                                >
                                    {this.props.chartTitle}:
                                </Typography>
                                <Typography
                                    color="textPrimary"
                                    variant="h4"
                                >
                                    Module未启用
                                </Typography>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
        } else {
            tag =
                <Card>
                    <CardContent>
                        <Grid
                            container
                            spacing={3}
                            sx={{ justifyContent: 'space-between' }}
                        >
                            <Grid item>
                                <Typography
                                    color="textSecondary"
                                    gutterBottom
                                    variant="h3"
                                >
                                    {this.props.chartTitle}:
                                </Typography>
                                <Typography
                                    color="textPrimary"
                                    variant="h3"
                                >
                                    {String(Data)}
                                </Typography>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
        }
        return tag

    }
}

export default BasicsTag;