        var title = 'CPU监控(单位：%)'

        let seriesList = []

        for (let i = 0; i < Info.length / 2; ++i) {
            var series = {}
            var moduleName = (headers[Info[i + i]] !== undefined) ? headers[Info[i + i]] : Info[i + i]
            series = {
                name: moduleName,
                data: Info[i + (i + 1)]
            }
            seriesList.push(series)
        }

