import React from 'react';
import {
    FormControl,
    Select,
    MenuItem,
    InputLabel,
} from '@material-ui/core';
import getCookie from '../../unit/getCookie';
import { clearAllCookie } from '../../unit/clearAllCookie';

class HostSelectList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            HostSelect: '',
        };
    }

    loadModuleDataFromUrl = () => {
        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))
        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/host-list", {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    var list = []
                    result.data.map((data) =>
                        list.push(data)
                    )
                    var select = <FormControl size="small" sx={{ width: '12%' }}>
                        <InputLabel>Host</InputLabel>
                        <Select
                            defaultValue=''
                            value={getCookie('host') === "" ? "" : getCookie('host')}
                            label="host"
                            onChange={this.handleChange}
                        >
                            {list.map((data, index) =>
                                <MenuItem value={data} key={index}>{data}</MenuItem>
                            )}
                        </Select>
                    </FormControl>

                    this.setState({
                        HostSelect: select,
                    });

                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    };
    componentDidMount() {
        this.loadModuleDataFromUrl();
        setInterval(this.loadModuleDataFromUrl, 3000);
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    handleChange = (e) => {
        document.cookie = 'host=' + e.target.value
        window.location.reload()
    }

    render() {
        return this.state.HostSelect
    }
}

export default HostSelectList;