import React from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
    Box,
    Card,
    CardHeader,
    Divider
} from '@material-ui/core';
import FadeMenu from '../dashboard/button/outbutton';
import { DataGrid } from '@mui/x-data-grid';

class ExamplesList extends React.Component {

    render() {
        var ListData = [
            {
                "id": 9,
                "datetime": 1650353675,
                "host": "192.168.2.115",
                "mac_address": "00:00:00:00:00:00",
                "device_group": "default",
                "interface_name": "enp2s0f0",
                "state": "down",
                "type": "ETHER"
            },
            {
                "id": 10,
                "datetime": 1650353675,
                "host": "192.168.2.115",
                "mac_address": "00:00:00:00:00:00",
                "device_group": "default",
                "interface_name": "wlp3s0",
                "state": "up",
                "type": "ETHER"
            },
            {
                "id": 11,
                "datetime": 1650353675,
                "host": "192.168.2.115",
                "mac_address": "00:00:00:00:00:00",
                "device_group": "default",
                "interface_name": "enp5s0",
                "state": "down",
                "type": "ETHER"
            },
            {
                "id": 12,
                "datetime": 1650353675,
                "host": "192.168.2.115",
                "mac_address": "00:00:00:00:00:00",
                "device_group": "default",
                "interface_name": "lo",
                "state": "unknown",
                "type": "LOOPBACK"
            }
        ]
        var columns = []
        var title = '网卡信息'
        var chartHeaders = '{"device_group":"设备组","interface_name":"接口名称","mac_address":"Mac地址","state":"接口状态","type":"接口类型"}'
        var obj = (chartHeaders.length !== 0) ? JSON.parse(chartHeaders) : {}
        for (var key in ListData[0]) {
            if (key !== "id" && key !== "host" && key !== "datetime") {
                var columsInfo = {}
                var name = (obj[key] !== undefined) ? obj[key] : key
                if (typeof (ListData[0][key]) === "string") {
                    columsInfo = {
                        field: key,
                        headerName: name,
                        width: 200,
                        type: 'String'
                    }
                } else {
                    columsInfo = {
                        field: key,
                        headerName: name,
                        width: 200,
                        type: 'number'
                    }
                }
                columns.push(columsInfo)
            }
        }

        const list =
            <Card sx={{ width: 'auto' }}>
                <CardHeader title={title} />
                <Divider />
                <PerfectScrollbar>
                    <div style={{ height: 420 }}>
                        <DataGrid
                            rows={ListData}
                            columns={columns}
                            pageSize={6}
                            rowsPerPageOptions={[6]}
                        />
                    </div>
                </PerfectScrollbar>
                <Box
                    sx={{
                        display: 'flex',
                        justifyContent: 'flex-end',
                        p: 1
                    }}
                >
                    <FadeMenu />

                </Box>
            </Card>
        return list
    }
}


export default ExamplesList;