//侧边栏
import PropTypes from 'prop-types';
import {
  Box,
  Drawer,
  Hidden,
  List,
} from '@material-ui/core';
import {
  Settings as SettingsIcon,
} from 'react-feather';
import ComputerIcon from '@mui/icons-material/Computer';
import NavItem from './NavItem';
import ArticleIcon from '@mui/icons-material/Article';

const items = [
  {
    href: '/app/dashboard',
    icon: ComputerIcon,
    title: '主机'
  },
  // {
  //   href: '/app/http',
  //   icon: ArticleIcon,
  //   title: 'Http监控'
  // },
  // {
  //   href: '/app/mysql',
  //   icon: ArticleIcon,
  //   title: 'MySQL监控'
  // },
  {
    href: '/app/settings',
    icon: SettingsIcon,
    title: '设置'
  },
  {
    href: '/app/examples',
    icon: ArticleIcon,
    title: '示例'
  },
];
const DashboardSidebar = ({ onMobileClose, openMobile }) => {
  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%'
      }}
    >
      <Box sx={{ p: 2 }}>
        <List>
          {items.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          ))}
        </List>
      </Box>
      <Box sx={{ flexGrow: 1 }} />
    </Box>
  );

  return (
    <>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
          PaperProps={{
            sx: {
              width: 500
            }
          }}
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden xlDown>
        <Drawer
          anchor="left"
          open
          variant="persistent"
          PaperProps={{
            sx: {
              width: 230,
              top: 64,
              height: 'calc(100% - 64px)'
            }
          }}
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

DashboardSidebar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

DashboardSidebar.defaultProps = {
  onMobileClose: () => {
  },
  openMobile: false
};

export default DashboardSidebar;
