import React from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
    Box,
    Card,
    CardHeader,
    Divider
} from '@material-ui/core';
import FadeMenu from '../button/outbutton';
import { DataGrid } from '@mui/x-data-grid';
import getCookie from '../../../unit/getCookie'
import { clearAllCookie } from '../../../unit/clearAllCookie';


var Data = []

class BasicList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ListData: [],
            chartTitle: '',
            chartDbName: '',
        };
    }

    loadModuleDataFromUrl = () => {
        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))
        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/list/" + this.props.chartDbName + "?host=" + this.props.host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    for (var i = 0; i < result.data.length; ++i) {
                        Data.push(result.data[i])
                    }
                    this.setState({
                        ListData: Data
                    });
                    Data = []
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    }

    componentDidMount() {
        this.loadModuleDataFromUrl();
        this.interval = setInterval(this.loadModuleDataFromUrl, 5000);
    };

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        const { ListData } = this.state;
        var columns = []
        var unit = '(单位：' + this.props.chartUnit + ')';
        var title = (this.props.chartUnit !== '') ? this.props.chartTitle + unit : this.props.chartTitle
        var obj = (this.props.chartHeaders.length !== 0) ? this.props.chartHeaders : {}
        for (var key in ListData[0]) {
            if (key !== "id" && key !== "host" && key !== "datetime") {
                var columsInfo = {}
                var name = (obj[key] !== undefined) ? obj[key] : key
                if (typeof (ListData[0][key]) === "string") {
                    columsInfo = {
                        field: key,
                        headerName: name,
                        width: 200,
                        type: 'String'
                    }
                } else {
                    columsInfo = {
                        field: key,
                        headerName: name,
                        width: 200,
                        type: 'number'
                    }
                }
                columns.push(columsInfo)
            }
        }

        const list =
            <Card sx={{ width: 'auto' }}>
                <CardHeader title={title} />
                <Divider />
                <PerfectScrollbar>
                    <div style={{ height: 420 }}>
                        <DataGrid
                            rows={ListData}
                            columns={columns}
                            pageSize={6}
                            rowsPerPageOptions={[6]}
                        />
                    </div>
                </PerfectScrollbar>
                <Box
                    sx={{
                        display: 'flex',
                        justifyContent: 'flex-end',
                        p: 1
                    }}
                >
                    <FadeMenu />

                </Box>
            </Card>
        return list
    }
}


export default BasicList;