import { useState } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  TextField
} from '@material-ui/core';
import ErrorMessage from '../dashboard/ErrorMessage';
import GetCookie from '../../unit/getCookie';
import { clearAllCookie } from '../../unit/clearAllCookie';

const SettingsPassword = (props) => {
  const [opne, setOpen] = useState(false)

  const [message, setMessage] = useState('')

  const [values, setValues] = useState({
    oldPassword: '',
    newPassword: ''
  });

  const handleChange = (event) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClick = () => {
    if (values.oldPassword === "") {
      setOpen(true);
      setMessage("原密码为空!!")
      console.log("原密码为空！！")
    } else if (values.newPassword === "") {
      setOpen(true);
      setMessage("新密码为空!!")
      console.log("新密码为空！！")
    } else if (values.newPassword.length < 6) {
      setOpen(true);
      setMessage("新密码长度小于6位!!")
      console.log("新密码长度小于6位！！")
    } else {
      let request = new Request(
        "http://" + window.zhiyan.host + "/ZhiYan/update/userinfo?old=" + values.oldPassword + "&new=" + values.newPassword,
        {
          method: 'post',
        }).catch(error => console.log(error + "   Error：userinfo未获取成功"))
      console.log(request)

      var myHeaders = new Headers();
      myHeaders.append('token', GetCookie('token'))
      fetch(request, {
        headers: myHeaders
      }).then(res => res.json()).then(res => {
        if (res.code === 400) {
          clearAllCookie();
          console.log("用户修改密码失败")
          window.location.href = '/login'
        }
        setOpen(true)
        setMessage(res.message)
        if (res.message === '用户修改密码成功') {
          window.location.href = '/login'
        }
      })
    }
  }
  return (
    <form {...props}>
      <Card>
        <CardHeader
          title="修改密码"
        />
        <ErrorMessage open={opne} message={message} close={handleClose} />
        <Divider />
        <CardContent>
          <TextField
            fullWidth
            label="原密码"
            margin="normal"
            name="oldPassword"
            onChange={handleChange}
            value={values.oldPassword}
            variant="outlined"
          />
          <TextField
            fullWidth
            label="新密码(长度6位以上)"
            margin="normal"
            name="newPassword"
            onChange={handleChange}
            type="password"
            value={values.newPassword}
            variant="outlined"
          />
        </CardContent>
        <Divider />
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'flex-end',
            p: 2
          }}
        >
          <Button
            color="primary"
            variant="contained"
            onClick={handleClick}
          >
            确定
          </Button>
        </Box>
      </Card>
    </form>
  );
};

export default SettingsPassword;
