import React from 'react';
import {
    Grid,
} from '@material-ui/core';
import { clearAllCookie } from '../../../unit/clearAllCookie';
import GetCookie from '../../../unit/getCookie';
import ConfigListCard from './configList';

class ConfigPages extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pages: '',
        };
    }

    existsToken = (TokenCode) => {
        if (TokenCode === -1) {
            clearAllCookie();
            window.location.href = '/login'
        }
    }

    loadModuleDataFromUrl = (host) => {
        var myHeaders = new Headers();
        myHeaders.append('token', GetCookie('token'))
        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/module-list?host=" + host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    var configList = []
                    for (let x = 0; x < result.data.length; ++x) {
                        configList.push(JSON.parse(String(result.data[x])))
                    }

                    var chart = configList.map((data, index) =>
                        <ConfigListCard key={index} config={data} />
                    )

                    this.setState({
                        pages: chart,
                    });
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    };

    componentDidMount() {
        this.existsToken(document.cookie.indexOf('token='))
        setInterval(this.existsToken, 30000, document.cookie.indexOf('token='));

        this.loadModuleDataFromUrl(GetCookie('host'));
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {

        return <Grid
            container
            spacing={4}
        >
            {this.state.pages}
        </Grid>

    }
}

export default ConfigPages;