import React from 'react';
import ZoomInIcon from '@mui/icons-material/ZoomIn';
import ZoomOutIcon from '@mui/icons-material/ZoomOut';
import {
    Box,
    CardContent,
    CardHeader,
    Divider,
    Card,
    IconButton,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Grid,
    Typography
} from '@material-ui/core';
import FadeMenu from '../button/outbutton';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import TimeTrans from '../../../unit/timeTrans';
import getCookie from '../../../unit/getCookie'
import { clearAllCookie } from '../../../unit/clearAllCookie';

var ModuleInfoList = []
var AllModuleInfoList = []

class BasicsChart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Info: [],
            TimeList: [],
            selectValue: ''
        };
    }

    loadModuleDataFromUrl = (time) => {
        if (this.state.selectValue !== '') {
            time = this.state.selectValue
        } else {
            time = '5minutes'
        }

        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))

        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/" + this.props.chartDbName + "?time=" + time + "&host=" + this.props.host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    } else {
                        let keyList = []
                        let DatetimeList = []
                        for (var key in result.data[0]) {
                            if (key !== "id" && key !== "host" && key !== "datetime") {
                                keyList.push(key)
                            }
                        }

                        for (let x = 0; x < keyList.length; ++x) {
                            for (let i = 0; i < result.data.length; ++i) {
                                DatetimeList.push(TimeTrans(result.data[i]['datetime']))
                                ModuleInfoList.push(result.data[i][keyList[x]])
                            }
                            AllModuleInfoList.push(keyList[x], [...ModuleInfoList])
                            ModuleInfoList.splice(0, ModuleInfoList.length)
                        }

                        this.setState({
                            isLoaded: true,
                            Info: [...AllModuleInfoList],
                            TimeList: DatetimeList
                        });
                        AllModuleInfoList.splice(0, AllModuleInfoList.length)
                    }
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    }

    handleChange = (e) => {
        this.setState({
            selectValue: e.target.value
        });
        setTimeout(this.loadModuleDataFromUrl, 0);
    }

    handleChartTypeChange = (e) => {
        this.setState({
            chartType: e.target.value
        });
        setTimeout(this.loadModuleDataFromUrl, 0);
    }

    componentDidMount() {
        this.loadModuleDataFromUrl(300);
        setInterval(this.loadModuleDataFromUrl, 5000, this.state.selectValue);
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        var headers = (this.props.chartHeaders.length !== 0) ? this.props.chartHeaders : {}
        var unit = '(单位：' + this.props.chartUnit + ')';

        var title = (this.props.chartUnit !== '') ? this.props.chartTitle + '监控' + unit : this.props.chartTitle + '监控'

        let seriesList = []
        const { Info } = this.state

        for (let i = 0; i < Info.length / 2; ++i) {
            var series = {}
            var modelName = (headers[Info[i + i]] !== undefined) ? headers[Info[i + i]] : Info[i + i]
            series = {
                name: modelName,
                data: Info[i + (i + 1)]
            }
            seriesList.push(series)
        }

        var options = {
            chart: {
                type: this.state.chartType
            },
            title: {
                text: null
            },
            credits: {
                enabled: false
            },
            yAxis: {
                title: {
                    text: null
                },
            },
            xAxis: {
                categories: this.state.TimeList,
                labels: {
                    enabled: false
                }
            },
            tooltip: {
                pointFormat: '<span style="color:{series.color}">{series.name}</span>: <b>{point.y}' + this.props.chartUnit + '</b><br/>',
                shared: true
            },
            legend: {
                layout: 'vertical',
                align: 'right',
                verticalAlign: 'middle'
            },
            plotOptions: {
                series: {
                    label: {
                        connectorAllowed: false
                    },
                }
            },
            series: seriesList,
            responsive: {
                rules: [{
                    condition: {
                        maxWidth: 500
                    },
                    chartOptions: {
                        legend: {
                            layout: 'horizontal',
                            align: 'center',
                            verticalAlign: 'bottom'
                        }
                    }
                }]
            }
        };

        var chart
        if (Info.length !== 0) {
            chart = <div>
                <Card>
                    <Box
                        sx={{
                            display: 'flex',
                            p: 1
                        }}>
                        <div style={{ height: '100%', width: '90%' }}>
                            <CardHeader
                                title={title}
                            />
                        </div>
                        <div style={{ width: 'auto' }}>
                            <FormControl>
                                <InputLabel>图表类型</InputLabel>
                                <Select
                                    label="图表类型"
                                    defaultValue='line'
                                    size='small'
                                    onChange={this.handleChartTypeChange}
                                >
                                    <MenuItem value={'line'}>折线图</MenuItem>
                                    <MenuItem value={'area'}>面积图</MenuItem>
                                    <MenuItem value={'bar'}>条形图</MenuItem>
                                    <MenuItem value={'scatter'}>散点</MenuItem>
                                    <MenuItem value={'column'}>堆叠柱形图</MenuItem>


                                </Select>
                            </FormControl>
                        </div>
                        <div style={{ width: 20 }}>
                        </div>
                        <div style={{ width: 'auto' }}>
                            <FormControl>
                                <InputLabel>时间范围</InputLabel>
                                <Select
                                    label="时间范围"
                                    defaultValue='5minutes'
                                    size='small'
                                    onChange={this.handleChange}
                                >
                                    <MenuItem value={'5minutes'}>5分钟</MenuItem>
                                    <MenuItem value={'10minutes'}>10分钟</MenuItem>
                                    <MenuItem value={'30minutes'}>30分钟</MenuItem>
                                </Select>
                            </FormControl>
                        </div>
                    </Box>
                    <Divider />
                    <CardContent>
                        <div style={{ height: '100%', width: '100%' }}>
                            <HighchartsReact
                                highcharts={Highcharts}
                                options={options}
                            />
                        </div>
                    </CardContent>
                    <Divider />
                    <Box
                        sx={{
                            display: 'flex',
                            justifyContent: 'left',
                            p: 1,
                            height: 75,
                        }}
                    >
                        <div style={{ width: "100%" }}>
                            <IconButton aria-label="add an alarm" size="large">
                                <ZoomInIcon fontSize="inherit" />
                            </IconButton>
                            <IconButton aria-label="add an alarm" size="large">
                                <ZoomOutIcon fontSize="inherit" />
                            </IconButton>
                        </div>

                        <div style={{ width: 'auto' }}>
                            <FadeMenu />
                        </div>
                    </Box>
                </Card>
            </div>
        } else {
            chart = <div>
                <Card>
                    <Box
                        sx={{
                            display: 'flex',
                            p: 1
                        }}>
                        <CardHeader
                            title={title}
                        />

                    </Box>
                    <Divider />
                    <CardContent>
                        <Grid
                            container
                            spacing={0}
                            direction="column"
                            alignItems="center"
                            justifyContent="center"
                            style={{ minHeight: '75vh' }}
                        >

                            <Grid item xs={3}>
                                <Typography
                                    color="textPrimary"
                                    gutterBottom
                                    variant="h4"
                                >
                                    无数据
                                </Typography>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </div>
        }

        return chart

    }
}

export default BasicsChart;