import React from 'react';
import {
    LinearProgress,
    Box,
    Card,
    CardContent,
    Grid,
    Typography,
} from '@material-ui/core';
import getCookie from '../../../unit/getCookie'
import { clearAllCookie } from '../../../unit/clearAllCookie';

class BacicsDetailedTag extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Data: String
        };
    }

    loadModuleDataFromUrl = () => {
        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))

        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/" + this.props.chartDbName + "/new?host=" + this.props.host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    var val = result.data[this.props.chartHeaders.keyword];
                    this.setState({
                        Data: val
                    });
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    }

    componentDidMount() {
        this.loadModuleDataFromUrl();
        this.interval = setInterval(this.loadModuleDataFromUrl, 5000);
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        const Data = this.state.Data;

        return <Card>
            <CardContent>
                <Grid
                    container
                    spacing={3}
                    sx={{ justifyContent: 'space-between' }}
                >
                    <Grid item>
                        <Typography
                            color="textSecondary"
                            gutterBottom
                            variant="h3"
                        >
                            {this.props.chartTitle}:
                        </Typography>
                        <Typography
                            color="textPrimary"
                            variant="h2"
                        >
                            {parseFloat(Data) + this.props.chartUnit}
                        </Typography>
                    </Grid>
                </Grid>
                <Box sx={{ pt: 3 }}>
                    <LinearProgress
                        value={parseInt(Data)}
                        variant="determinate"
                    />
                </Box>
            </CardContent>
        </Card>
    }
}

export default BacicsDetailedTag;