import React from 'react';
import {
    Card,
    CardContent,
    Grid,
    Box,
    Typography
} from '@material-ui/core';
import ChartTypeSelect from './ChartTypeSelect';
import GetCookie from '../../unit/getCookie';
import { clearAllCookie } from '../../unit/clearAllCookie';

function sortByKey(array, key) {
    return array.sort(function(a, b) {
        var x = a[key]; var y = b[key];
        return ((x < y) ? -1 : ((x > y) ? 1 : 0));
    });
}

class ModuleChartList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ModuleStatList: [],
        };
    }

    existsToken = (TokenCode) => {
        if (TokenCode === -1) {
            clearAllCookie();
            window.location.href = '/login'
        }
    }

    loadModuleDataFromUrl = (host) => {
        var myHeaders = new Headers();
        myHeaders.append('token', GetCookie('token'))
        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/module-list?host=" + host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    var test = []
                    for (let x = 0; x < result.data.length; ++x) {
                        test.push(JSON.parse(String(result.data[x])))
                    }
                    this.setState({
                        ModuleStatList: test
                    });
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    };
    componentDidMount() {
        this.existsToken(document.cookie.indexOf('token='))
        setInterval(this.existsToken, 30000, document.cookie.indexOf('token='));

        if (this.props.host !== '') {
            this.loadModuleDataFromUrl(this.props.host);
            setInterval(this.loadModuleDataFromUrl, 3000, this.props.host);
        }
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        const { ModuleStatList } = this.state

        let chartList = []
        let tagList = []

        var dashboard = ''
        for (let x = 0; x < ModuleStatList.length; ++x) {
            var moduleName = (ModuleStatList[x].chinese_name !== null && ModuleStatList[x].chinese_name !== '') ? ModuleStatList[x].chinese_name : ModuleStatList[x].mod_name
            const series = {
                name: moduleName,
                db_name: ModuleStatList[x].mod_name,
                type: ModuleStatList[x].chart_type,
                headers: ModuleStatList[x].headers,
                unit: ModuleStatList[x].unit
            }

            if (ModuleStatList[x].headers.hasOwnProperty('tag')) {
                var mod_name = ModuleStatList[x].mod_name
                var unit = ModuleStatList[x].unit
                var jsonHeaders = ModuleStatList[x].headers
                if (jsonHeaders.tag.length >= 2) {
                    var detailedtags = []
                    for (let x = 0; x < jsonHeaders.tag.length; ++x) {
                        detailedtags.push(jsonHeaders.tag[x])
                    }
                    const seriesaa = {
                        db_name: mod_name,
                        type: 'detailedtags',
                        headers: detailedtags,
                        unit: unit
                    }
                    tagList.push(seriesaa)
                } else {
                    const seriesaa = {
                        name: jsonHeaders.tag[0].name,
                        db_name: mod_name,
                        type: 'detailedtag',
                        headers: jsonHeaders.tag[0],
                        unit: unit
                    }
                    tagList.push(seriesaa)
                }
            }

            if (ModuleStatList[x].chart_type !== "tag") {
                chartList.push(series)
            } else {
                tagList.push(series)
            }
        }
        
        sortByKey(chartList, 'type');
        sortByKey(tagList, 'type');

        const chartDashboard =
            chartList.map((data, index) =>
                <ChartTypeSelect chartType={data.type} chartTitle={data.name} chartDbName={data.db_name} chartHeaders={data.headers} chartUnit={data.unit} key={index} />
            )

        const tagDashboard =
            tagList.map((data, index) =>
                <ChartTypeSelect chartType={data.type} chartTitle={data.name} chartDbName={data.db_name} chartHeaders={data.headers} chartUnit={data.unit} key={index} />
            )

        var moduleNum = this.state.ModuleStatList.length

        if (this.props.host === '') {
            dashboard = <Grid
                container
                spacing={0}
                direction="column"
                alignItems="center"
                justifyContent="center"
                style={{ minHeight: '100vh' }}
            >

                <Grid item xs={3}>
                    <Typography
                        color="textPrimary"
                        gutterBottom
                        variant="h3"
                    >
                        当前Host为空,请添加Host地址于左上输入框。
                    </Typography>
                </Grid>
            </Grid>
        } else if (moduleNum === 0) {
            dashboard = <Grid
                container
                spacing={0}
                direction="column"
                alignItems="center"
                justifyContent="center"
                style={{ minHeight: '100vh' }}
            >

                <Grid item xs={3}>
                    <Typography
                        color="textPrimary"
                        gutterBottom
                        variant="h3"
                    >
                        Module列表为空,请添加Module于右上按钮。
                    </Typography>
                </Grid>
            </Grid>
        } else {
            dashboard = <div style={{ width: '100%' }}>
                <Grid
                    container
                    spacing={2}
                >
                    {tagDashboard}
                </Grid>
                <Box
                    sx={{ height: '1%', width: 'auto' }}
                >
                </Box>
                <Card
                    sx={{ height: 'auto', width: 'auto' }}
                >
                    <CardContent>
                        <Grid
                            container
                            spacing={3}
                        >
                            {chartDashboard}
                        </Grid>
                    </CardContent>
                </Card>
            </div>
        }

        return dashboard
    }
}


export default ModuleChartList;