import React from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  TextField,
} from '@material-ui/core';
import Base64 from 'crypto-js/enc-base64'
import HmacSHA256 from 'crypto-js/hmac-sha256';
import TokenList from './TokenList';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import ErrorMessage from '../dashboard/ErrorMessage';
import GetCookie from '../../unit/getCookie';
import { clearAllCookie } from '../../unit/clearAllCookie';

function isValidIP(ip) {
  var reg = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/
  return reg.test(ip);
}

class SettingsToken extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      token: '',
      host: '',
      copied: false,
      isopen: false,
      message: ','
    };
  }

  handClose = () => {
    this.setState({
      isopen: false
    });
  }

  handleChange = (e) => {
    this.setState({
      host: e.target.value
    });
  }
  handleClick = () => {
    if (this.state.host === "") {
      this.setState({
        isopen: true,
        message: "Host为空！！！"
      })
      console.log("Host为空！！！")
    } else if (!isValidIP(this.state.host)) {
      this.setState({
        isopen: true,
        message: "Host格式错误！！！"
      })
      console.log("Host格式错误！！！")
    } else {
      let datetime = Math.round(new Date().getTime() / 1000).toString();
      var searchRegExp = /\+/g
      var searchReg = /\//g
      var token = Base64.stringify(HmacSHA256(datetime + this.state.host, datetime)).replace(searchRegExp, "").replace(searchReg, "")
      this.setState({
        token: token
      })
    }
  };

  saveHandleClick = () => {
    let datetime = Math.round(new Date().getTime() / 1000).toString();
    let savaHost = this.state.host
    let savaToken = this.state.token
    if (savaToken === "" || savaHost === "") {
      this.setState({
        isopen: true,
        message: "请先生成令牌！！！"
      })
      console.log("请先生成令牌！！！")
    } else {
      let request = new Request(
        "http://" + window.zhiyan.host + "/ZhiYan/add/token?datetime=" + datetime + "&host=" + savaHost + "&token=" + savaToken,
        {
          method: 'post',
        })
      console.log(request)
      console.log("Token保存成功")


      var myHeaders = new Headers();
      myHeaders.append('token', GetCookie('token'))

      fetch(request, {
        headers: myHeaders
      }).then(res => res.json())
        .then(res => {
          console.log(res.code)
          if (res.code === 400) {
            clearAllCookie();
            console.log("token已过期，请重新登录")
            window.location.href = '/login'
          }
          this.setState({
            isopen: true,
            message: res.message
          })
        }).catch(error => console.log(error + "   Error：token已过期，请重新登陆"))
    }
  };

  render() {

    const token =
      <Card>
        <ErrorMessage open={this.state.isopen} message={this.state.message} close={this.handClose} />
        <CardHeader
          title="令牌"
        />
        <Divider />
        <CardContent>
          <Box
            sx={{
              p: 2
            }}
          >
            <TokenList />
          </Box>
          <Box
            sx={{
              display: 'flex',
              p: 2
            }}
          >
            <TextField
              fullWidth
              label="Host"
              margin="normal"
              onChange={this.handleChange}
              variant="outlined"
              sx={{
                width: '88%'
              }}
            />
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'flex-end',
                p: 2,
              }}
            >
              <Button
                color="primary"
                variant="contained"
                onClick={this.handleClick}
                sx={{
                  height: 55,
                  width: 120
                }}
              >
                生成令牌
              </Button>
            </Box>
          </Box>

          <Box
            sx={{
              display: 'flex',
              p: 2
            }}
          >
            <TextField
              disabled
              fullWidth
              label="令牌"
              margin="normal"
              value={this.state.token}
              variant="outlined"
              sx={{
                width: '88%'
              }}
            />
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'flex-end',
                p: 2,
              }}
            >
              <CopyToClipboard text={this.state.token}
                onCopy={() => this.setState({ copied: true })}>
                <Button
                  color="primary"
                  variant="contained"
                  sx={{
                    height: 53,
                    width: 120
                  }}
                >
                  复制
                </Button>
              </CopyToClipboard>
            </Box>
          </Box>
        </CardContent>
        <Divider />
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'flex-end',
            p: 2
          }}
        >
          <Button
            color="primary"
            variant="contained"
            onClick={this.saveHandleClick}
          >
            保存
          </Button>
        </Box>
      </Card>
    return token
  }
}

export default SettingsToken;