import React from 'react';
import CachedIcon from '@mui/icons-material/Cached';
import {
    Box,
    Grid,
    IconButton,
    Card,
    CardContent,
    CardHeader,
    Divider,
    TextField,
} from '@material-ui/core';
import GetCookie from '../../../unit/getCookie';
import { clearAllCookie } from '../../../unit/clearAllCookie';
import ErrorMessage from '../ErrorMessage';

class ConfigListCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            config: this.props.config,
            isopen: false,
            message: ''
        };
    }

    handClose = () => {
        this.setState({
            isopen: false,
        });
    }

    handleChange = (e) => {
        var configJson = this.props.config
        configJson[e.target.id] = e.target.value

        this.setState({
            config: configJson
        });

    }

    handleClick = () => {
        let request = new Request(
            "http://" + window.zhiyan.host + "/ZhiYan/update/config?name=" + this.props.config.mod_name + "&config=" + encodeURI(JSON.stringify(this.state.config)) + "&host=" + GetCookie('host'),
            {
                method: 'post',
            })

        console.log(request)
        console.log("Config更新")

        var myHeaders = new Headers();
        myHeaders.append('token', GetCookie('token'))

        fetch(request, {
            headers: myHeaders
        }).then(res => res.json())
            .then(res => {
                console.log(res.code)
                if (res.code === 400) {
                    clearAllCookie();
                    console.log("token已过期，请重新登录")
                    window.location.href = '/login'
                }

                this.setState({
                    isopen: true,
                    message: res.message
                })

            }).catch(error => console.log(error + "   Error：token已过期，请重新登陆"))
    }

    render() {

        var json = {
            "active": "运行状态",
            "chart_type": "图表类型",
            "chinese_name": "中文名称",
            "debug": "日志等级",
            "dry_run": "试运行",
            "headers": "字段映射",
            "host": "主机地址",
            "interval": "采集间隔",
            "mod_name": "模块名称",
            "unit": "单位"
        }

        var keyList = []
        var valueList = []

        for (var key in this.props.config) {
            keyList.push(key)
            valueList.push(this.props.config[key])
        }

        var list = <Grid
            container
        >
            {keyList.map((data, index) =>
                <div key={index}>
                    <TextField
                        id={data}
                        sx={{ width: "80%" }}
                        size='small'
                        margin="normal"
                        onChange={this.handleChange}
                        label={json[data]}
                        value={
                            String(this.state.config[data]).startsWith('[') ? JSON.stringify(this.state.config[data]) : this.state.config[data]
                        }
                        variant="outlined"
                    />
                </div>
            )}
        </Grid>
        console.log(GetCookie('host'))

        const token =
            <Grid
                item
                xl={6}
                xs={12}
            >
                <ErrorMessage open={this.state.isopen} message={this.state.message} close={this.handClose} />
                <Card>
                    <Box
                        sx={{
                            display: 'flex',
                        }}>
                        <CardHeader
                            title={this.props.config.mod_name}
                        />
                        <div style={{ width: "90%" }}></div>

                        <IconButton onClick={this.handleClick}>
                            <CachedIcon />
                        </IconButton>
                    </Box>
                    <Divider />
                    <CardContent>
                        {list}
                    </CardContent>
                </Card>
            </Grid>
        return token
    }
}

export default ConfigListCard;