import React from 'react';
import {
    Box,
    Grid,
    Card,
    CardHeader,
    Divider,
    CardContent,
    Typography
} from '@material-ui/core';
import BasicsChart from './basics/basicsChart';
import BasicsTag from './basics/basicsTag';
import BasicList from './basics/basicsList';
import BacicsChartList from './basics/bacicsChartList';
import getCookie from '../../unit/getCookie';
import BacicsDetailedTag from './basics/bacicsDetailedTag';
import BacicsDetailedTags from './basics/bacicsDetailedTags';


class ChartTypeSelect extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Info: [],
            chartType: null,
            chartTitle: '',
            chartDbName: '',
            chartStat: false,
        };
    }

    render() {
        var TypeChart;
        if (this.props.chartStat === false && this.props.chartType !== "little") {
            TypeChart =
                <Grid
                    item
                    xl={2}
                    xs={12}
                >
                    <div>
                        <Card>
                            <Box
                                sx={{
                                    display: 'flex',
                                    p: 1
                                }}>
                                <CardHeader
                                    title={this.props.chartTitle + "监控"}
                                />

                            </Box>
                            <Divider />
                            <CardContent>
                                <Grid
                                    container
                                    spacing={0}
                                    direction="column"
                                    alignItems="center"
                                    justifyContent="center"
                                    style={{ minHeight: '5vh' }}
                                >

                                    <Grid item xs={3}>
                                        <Typography
                                            color="textPrimary"
                                            gutterBottom
                                            variant="h4"
                                        >
                                            Model未启用
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </div>
                </Grid>
        } else {
            switch (this.props.chartType) {
                case 'chart':
                    TypeChart =
                        <Grid
                            item
                            xl={4}
                            xs={12}
                        >
                            <BasicsChart chartTitle={this.props.chartTitle} chartDbName={this.props.chartDbName} chartHeaders={this.props.chartHeaders} chartUnit={this.props.chartUnit} host={getCookie('host')} />
                        </Grid>;
                    break;
                case 'list':
                    TypeChart =
                        <Grid
                            item
                            xl={6}
                        >
                            <BasicList chartTitle={this.props.chartTitle} chartDbName={this.props.chartDbName} chartHeaders={this.props.chartHeaders} chartUnit={this.props.chartUnit} host={getCookie('host')} />
                        </Grid>
                    break;
                case 'tag':
                    TypeChart =
                        <Grid
                            item
                            xl={2}
                        >
                            <BasicsTag chartTitle={this.props.chartTitle} chartDbName={this.props.chartDbName} chartStat={this.props.chartStat} host={getCookie('host')} />
                        </Grid>
                    break;
                case 'chartlist':
                    TypeChart =
                        <Grid
                            item
                            xl={12}
                        >
                            <BacicsChartList chartTitle={this.props.chartTitle} chartDbName={this.props.chartDbName} chartHeaders={this.props.chartHeaders} chartUnit={this.props.chartUnit} host={getCookie('host')} />
                        </Grid>
                    break;
                case 'detailedtag':
                    TypeChart =
                        <Grid
                            item
                            xl={2}
                        >
                            <BacicsDetailedTag chartTitle={this.props.chartTitle} chartDbName={this.props.chartDbName} chartHeaders={this.props.chartHeaders} chartUnit={this.props.chartUnit} host={getCookie('host')} />
                        </Grid>
                    break;
                case 'detailedtags':
                    TypeChart =
                        <Grid
                            item
                        >
                            <BacicsDetailedTags chartTitle={this.props.chartTitle} chartDbName={this.props.chartDbName} chartHeaders={this.props.chartHeaders} chartUnit={this.props.chartUnit} host={getCookie('host')} />
                        </Grid>
                    break;
                default:
                    TypeChart =
                        <Grid
                            item
                            xl={2}
                        >
                            Error
                        </Grid>
            }
        }
        return TypeChart
    }
}

export default ChartTypeSelect;