import * as React from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import { Helmet } from 'react-helmet';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import LoginLogo from '../components/singnIn/LoginLogo';
import ErrorMessage from '../components/dashboard/ErrorMessage';
import HmacSHA256 from 'crypto-js/hmac-sha256';
import Base64 from 'crypto-js/enc-base64';
import { generateUUID } from '../unit/createUUID';

const theme = createTheme();

export default function SignIn() {
  const handleSubmit = (event) => {
    event.preventDefault();
  };

  const [userName, setUserName] = React.useState('')

  const [password, setPassword] = React.useState('')

  const [opne, setOpen] = React.useState(false)

  const [message, setMessage] = React.useState('')


  const handleUserName = (e) => {
    setUserName(e.target.value)
  }

  const handlePassword = (e) => {
    setPassword(e.target.value)
  }

  const handleClose = () => {
    setOpen(false);
  };

  const handleClick = (e) => {
    if (userName === "") {
      setOpen(true);
      setMessage("用户名为空！！")
      console.log(window.zhiyan.host)

      console.log("用户名为空！！！")
    } else if (password === "") {
      setOpen(true);
      setMessage("密码为空！！")
      console.log("密码为空！！！")
    } else {
      let timestamp = Math.round(new Date().getTime() / 1000).toString();

      var signatureMethod = "HmacSHA256"
      var expectedAccessKeySecret = "zhiyan"
      var signatureNonce = generateUUID();
      var searchRegExp = /\+/g
      var replaceWith = "%2B"

      var signature = Base64.stringify(HmacSHA256(userName + password, expectedAccessKeySecret))

      var resultSignature = signature.replace(searchRegExp, replaceWith)

      let testRequest = new Request(
        "http://" + window.zhiyan.host + "/ZhiYan/login/userinfo?username=" + userName + "&password=" + password + "&timestamp=" + timestamp + "&signatureNonce=" + signatureNonce + "&signatureMethod=" + signatureMethod + "&signature=" + resultSignature,
        {
          method: 'post',
        })
      console.log(testRequest)

      fetch(testRequest)
        .then(res => res.json())
        .then(res => {
          if (res.message === "登录成功") {
            document.cookie = 'username=' + userName
            document.cookie = "token=" + res.token

            var myHeaders = new Headers();
            myHeaders.append('token', res.token)

            fetch("http://" + window.zhiyan.host + "/ZhiYan/get/host-list", {
              headers: myHeaders
            })
              .then(res => res.json())
              .then(
                (result) => {
                  if (result.code === 200) {
                    document.cookie = 'host=' + result.data[0]
                    window.location.href = '/app/dashboard'
                  } else {
                    setOpen(true);
                    setMessage("信息未获取成功，登陆失败！！！")
                  }
                }).catch(error => console.log(error + "   Error：host-list未获取成功"))
          } else {
            setOpen(true);
            setMessage(res.message)
          }
        }).catch(error => console.log(error + "   Error: zhiyan-web-server连接失败！！！请检查zhiyan-web-server是否启动！！！"))
    }
  }
  return (
    <ThemeProvider theme={theme}>
      <Helmet>
        <title>智眼</title>
      </Helmet>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <ErrorMessage open={opne} message={message} close={handleClose} />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <LoginLogo />
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="用户名/邮箱/电话"
              name="email"
              autoComplete="email"
              autoFocus
              onChange={handleUserName}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="密码"
              type="password"
              id="password"
              autoComplete="current-password"
              onChange={handlePassword}
            />

            <Grid container>
              <Grid item xs>
                <FormControlLabel
                  control={<Checkbox value="remember" color="primary" />}
                  label="记住我"
                />
              </Grid>
              <Grid item>
                <Link href="#" variant="body2">
                  忘记密码？
                </Link>
              </Grid>
            </Grid>

            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              onClick={handleClick}
            >
              登录
            </Button>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}