import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import TimeTrans from '../../unit/timeTrans';
import GetCookie from '../../unit/getCookie';
import { clearAllCookie } from '../../unit/clearAllCookie';

class TokenList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Data: []
        };
    }


    existsToken = (TokenCode) => {
        if (TokenCode === -1) {
            clearAllCookie();
            window.location.href = '/login'
        }
    }

    loadModuleDataFromUrl = () => {
        var myHeaders = new Headers();
        myHeaders.append('token', GetCookie('token'))

        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/token-list", {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token-list未获取成功")
                        window.location.href = '/login'
                    } else {
                        this.setState({
                            Data: result.data
                        });
                    }
                }
            ).catch(error => console.log(error + "   Error：token-list未获取成功"))
    }

    componentDidMount() {
        this.existsToken(document.cookie.indexOf('token='))
        setInterval(this.existsToken, 30000, document.cookie.indexOf('token='));
        this.loadModuleDataFromUrl();
        this.interval = setInterval(this.loadModuleDataFromUrl, 5000);
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        var tokenList = this.state.Data

        return <TableContainer sx={{ width: '99%' }} component={Paper}>
            <Table aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell>Host</TableCell>
                        <TableCell>令牌</TableCell>
                        <TableCell>创建时间</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {tokenList.map((Data) => (
                        <TableRow
                            key={Data.host + Data.token}
                        >
                            <TableCell>{Data.host}</TableCell>
                            <TableCell>{Data.token}</TableCell>
                            <TableCell>{TimeTrans(Data.datetime)}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>

    }
}

export default TokenList;
