import React from 'react';
import {
    LinearProgress,
    Box,
    Card,
    CardContent,
    Grid,
    Typography,
    Divider,
} from '@material-ui/core';
import getCookie from '../../../unit/getCookie'
import { clearAllCookie } from '../../../unit/clearAllCookie';

class BacicsDetailedTags extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Data: String
        };
    }

    loadModuleDataFromUrl = () => {
        var myHeaders = new Headers();
        myHeaders.append('token', getCookie('token'))

        fetch("http://" + window.zhiyan.host + "/ZhiYan/get/" + this.props.chartDbName + "/new?host=" + this.props.host, {
            headers: myHeaders
        })
            .then(res => res.json())
            .then(
                (result) => {
                    if (result.code === 400) {
                        clearAllCookie();
                        console.log("token已过期，请重新登录")
                        window.location.href = '/login'
                    }
                    this.setState({
                        Data: result.data
                    });
                }
            ).catch(error => console.log(error + "   Error：token已过期，请重新登录"))
    }

    componentDidMount() {
        this.loadModuleDataFromUrl();
        this.interval = setInterval(this.loadModuleDataFromUrl, 5000);
    }

    componentWillUnmount() {
        this.setState = () => false;
    }

    render() {
        const Data = this.state.Data;
        var info = []
        var total = ''
        for (let x = 0; x < this.props.chartHeaders.length; ++x) {
            if (this.props.chartHeaders[x].keyword !== "total") {
                info.push(this.props.chartHeaders[x])
            } else {
                total = this.props.chartHeaders[x]
            }
        }
        var basic = ""
        if (total === '') {
            basic =
                <Card>
                    <CardContent>
                        <Grid
                            container
                            spacing={2}
                        >{this.props.chartHeaders.map((data) =>
                            <Grid
                                item
                                key={data.name}
                            >
                                <Card>
                                    <CardContent>
                                        <Grid
                                            container
                                            spacing={2}
                                            sx={{ justifyContent: 'space-between' }}
                                        >
                                            <Grid item>
                                                <Typography
                                                    color="textSecondary"
                                                    gutterBottom
                                                    variant="h5"
                                                >
                                                    {data.name}
                                                </Typography>
                                                <Typography
                                                    color="textPrimary"
                                                    variant="h4"
                                                >
                                                    {Data[data.keyword] + this.props.chartUnit}
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                        <Box sx={{ pt: 1 }}>
                                        </Box>
                                    </CardContent>
                                </Card>
                            </Grid>
                        )}
                        </Grid>
                    </CardContent>
                </Card>
        } else {
            if (info.length === 1) {
                basic =
                    <Card>
                        <CardContent>
                            <Grid
                                container
                                spacing={2}
                                sx={{ justifyContent: 'space-between' }}
                            >
                                <Grid item>
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="h4"
                                    >
                                        {info[0].name + "(" + total.name + ':' + Data['total'] + this.props.chartUnit + ")"}
                                    </Typography>
                                    <Typography
                                        color="textPrimary"
                                        variant="h3"
                                    >
                                        {Data[info[0].keyword] + this.props.chartUnit}
                                    </Typography>
                                </Grid>
                            </Grid>
                            <Box sx={{ pt: 1 }}>
                                <Typography
                                    color="textPrimary"
                                    variant="h4"
                                    align="right"
                                >
                                    {((parseInt(Data[info[0].keyword]) / parseInt(Data['total'])) * 100).toFixed(1)}%
                                </Typography>
                                <Divider />
                                <LinearProgress
                                    value={(parseInt(Data[info[0].keyword]) / parseInt(Data['total'])) * 100}
                                    variant="determinate"
                                />
                            </Box>
                        </CardContent>
                    </Card>
            } else {
                basic =
                    <Card>
                        <CardContent>
                            <Grid
                                container
                                spacing={2}
                            >
                                {info.map((data, index) =>
                                    <Grid
                                        item
                                        key={data.name}
                                    >
                                        <Card key={index}>
                                            <CardContent>
                                                <Grid
                                                    container
                                                    spacing={2}
                                                    sx={{ justifyContent: 'space-between' }}
                                                >
                                                    <Grid item>
                                                        <Typography
                                                            color="textSecondary"
                                                            gutterBottom
                                                            variant="h5"
                                                        >
                                                            {data.name + "(" + total.name + ':' + Data['total'] + this.props.chartUnit + ")"}
                                                        </Typography>
                                                        <Typography
                                                            color="textPrimary"
                                                            variant="h4"
                                                        >
                                                            {Data[data.keyword] + this.props.chartUnit}
                                                        </Typography>
                                                    </Grid>
                                                </Grid>
                                                <Box sx={{ pt: 1 }}>
                                                    <Typography
                                                        color="textPrimary"
                                                        variant="h4"
                                                        align="right"
                                                    >
                                                        {((parseInt(Data[data.keyword]) / parseInt(Data['total'])) * 100).toFixed(1)}%
                                                    </Typography>
                                                    <Divider />
                                                    <LinearProgress
                                                        value={(parseInt(Data[data.keyword]) / parseInt(Data['total'])) * 100}
                                                        variant="determinate"
                                                    />
                                                </Box>
                                            </CardContent>
                                        </Card>
                                    </Grid>
                                )}
                            </Grid>
                        </CardContent>
                    </Card>
            }
        }
        return basic
    }
}

export default BacicsDetailedTags;